	module.exports = {"data":{"post":{"html":"<p>This afternoon at 4 pm we'll be at the <a href=\"http://sports.yahoo.com/sow/news?slug=reu-worlddc&amp;prov=reuters&amp;type=lgns\">Czech Republic - Italy</a> match in Hamburg, this time with real tickets, no FIFA buffet but with Christopher. We're lucky that both teams have something to gain, so we won't have a yawner like <a href=\"http://sports.yahoo.com/sow/news;_ylt=AnZZvatp4tMGroOhQew3I9gmw7YF?slug=ap-wcup-netherlands-argentina&amp;prov=ap&amp;type=lgns\">Argentina - Holland</a> last night.</p>\n<p>We'll be sitting in the \"north curve\" just behind the right post of the right-hand goal (if you're watching on TV). Christopher might be holding up his American flag. Wave if you see us.</p>\n<p><strong>Update:</strong> We're back. Italy won and we had a good look at the second goal on our end. Here are a couple of pictures</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/172592607/\"><img src=\"https://static.flickr.com/44/172592607_0d37c6fb77.jpg\" alt=\"Christopher at the Arena\" title=\"Christopher at the Arena\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/papascott/172592312/\"><img src=\"https://static.flickr.com/54/172592312_bc270cc933.jpg\" alt=\"The view from our seats\" title=\"The view from our seats\" /></a></p>","fields":{"title":"Wave If You See Us","date":"22 Jun 2006","url":"https://papascott.de/2006-06-22-wave-if-you-see-us/"}}},"pathContext":{"slug":"/2006-06-22-wave-if-you-see-us/","prev":{"fields":{"title":"MacBook Shanghaied","date":"21 Jun 2006","slug":"/2006-06-21-macbook-shanghaied/"}},"next":{"fields":{"title":"Web Design Explained","date":"23 Jun 2006","slug":"/2006-06-23-web-design-explained/"}}}}

